import React, { Component } from 'react';

class ProfileHighlights extends Component {

    constructor(props){
        super(props)
        this.state = {
            profile: 'Developer',
            location: 'Bangalore, India',
            email: 'nameesh.gabru@gmail.com',
            phone: '+91 7718 920631',
            skills: [
                {name: 'Node.js', href: 'https://nodejs.org/en/', percent: '80%'},
                {name: 'Electron', href: 'https://electron.atom.io/', percent: '70%'},
                {name: 'Express', href: 'https://expressjs.com/', percent: '75%'},
                {name: 'SOCKET.IO', href: 'https://socket.io/', percent: '50%'},
                {name: 'React', href: 'https://facebook.github.io/react/', percent: '40%'},
                {name: 'D3.js', href: 'https://d3js.org/', percent: '40%'},
                {name: 'three.js', href: 'https://threejs.org/', percent: '40%'},
                {name: 'MongoDB', href: 'https://www.mongodb.com/', percent: '70%'},
                {name: 'OPENSHIFT', href: 'https://www.openshift.com/', percent: '50%'},
                {name: 'aws', href: 'https://aws.amazon.com/', percent: '60%'}
            ],
            programmingLanguages: [
                {name:'JavaScript', percent:'70%'},
                {name:'Python', percent:'40%'},
                {name:'Java', percent:'35%'}
            ]
        }
    }
    
    render() {

        //skills list
        const skills = this.state.skills.map((skill, i) =>
            <div key={i}>
                <p style={{marginBottom:'2px'}}><a href={skill.href} target="_blank" rel="noopener noreferrer">{skill.name}</a></p>
                <div className="w3-light-grey w3-round w3-tiny">
                    <div className="w3-container w3-center w3-round w3-teal" style={{width:skill.percent}}>{skill.percent}</div>
                </div>
            </div>
        );

        //languages list
        const programmingLanguages = this.state.programmingLanguages.map((lang, i) =>
            <div key={i}>
                <p style={{marginBottom:'2px'}}>{lang.name}</p>
                <div className="w3-light-grey w3-round">
                    <div className="w3-round w3-teal" style={{height:'15px', width:lang.percent}}></div>
                </div>
            </div>
        );

        return (
            <div className="w3-container">
        
                <p><i className="fa fa-briefcase fa-fw w3-margin-right w3-large w3-text-teal"></i>{this.state.profile}</p>
                <p><i className="fa fa-home fa-fw w3-margin-right w3-large w3-text-teal"></i>{this.state.location}</p>
                <p><i className="fa fa-envelope fa-fw w3-margin-right w3-large w3-text-teal"></i>{this.state.email}</p>
                <p><i className="fa fa-phone fa-fw w3-margin-right w3-large w3-text-teal"></i>{this.state.phone}</p>
                <hr />

                <p className="w3-large"><b><i className="fa fa-cog fa-spin fa-fw w3-margin-right w3-text-teal w3-large"></i>Tools</b></p>
                {skills}
                <br />

                <p className="w3-large w3-text-theme"><b><i className="fa fa-code fa-fw w3-margin-right w3-text-teal w3-large"></i>Programming Languages</b></p>
                {programmingLanguages}
                <br />
            </div>
        );
    }
}

export default ProfileHighlights;


