import React, { Component } from 'react';
// sample props 
// tabs:[
//     {
//         name:'IIT Kanpur',
//         contents:['Bachelor of Technology in Mechanical Engineering'], 
//         period:'Jul 2003 - May 2008', 
//         href:'http://www.iitk.ac.in/'
//     },
//     {
//         name:'DAV College Jalandhar', 
//         contents:['Senior Secondary'], 
//         period: 'Mar 2001 - Mar 2003', 
//         href:'http://www.davjalandhar.com/'
//     }
// ]

class ButtonTabs extends Component {

    constructor(props) {
        super (props)
        //setting tabs state
        const tabs = [];
        for(var l=0; l<props.tabs.length; l++){
            const tempTab = Object.assign({}, props.tabs[l]);
            if(l){
                tempTab.active = false
            }else{
                tempTab.active = true
            }
            tempTab.key = l
            tabs.push(tempTab)
        }
        this.state = {tabs}
    }

    changeTab(e){
        const tabSelected = parseInt(e.target.dataset.key, 0)
        const tabs = [];
        for(var l=0; l<this.props.tabs.length; l++){
            const tempTab = Object.assign({}, this.props.tabs[l]);
            if(l === tabSelected){
                tempTab.active = true
            }else{
                tempTab.active = false
            }
            tempTab.key = l
            tabs.push(tempTab)
        }
        this.setState({tabs})
    }

    render() {
        
        //button Lists
        const buttons = this.state.tabs.map((tab) =>
            <button style={{margin:'3px'}} key = {tab.key} className = {(tab.active) ? 'w3-button w3-button-active w3-hover-green w3-bar-item w3-round w3-small': 'w3-button w3-hover-green w3-bar-item w3-round w3-small'} onClick={this.changeTab.bind(this)} data-key={tab.key}>{tab.name}</button>
        );
        //A HELPER COMPONENT
        function ToParagraphs (props){
            const paragraphs = props.contents.map((content, i) =>
                <p key={i} style={{textAlign:'justify'}}>{content}</p>
            );
            return (
                <div>{paragraphs}</div>
            );
        }
        //visible Tab COMPONENT
        function VisibleTab(props){
            let tab = {};
            for(var l=0; l<props.tabs.length; l++){
                const tempTab = props.tabs[l]
                if(tempTab.active){
                    tab = Object.assign({}, tempTab);
                }
            }
            return (
                <div>
                    <h4 className="w3-text-grey w3-padding-16"><a href={tab.href} target="_blank" rel="noopener noreferrer">{tab.name}</a></h4>
                    {tab.period ? <h6 className="w3-text-teal"><i className="fa fa-calendar fa-fw w3-margin-right"></i>{tab.period}</h6>: ''}
                    <ToParagraphs contents={tab.contents} />
                </div>
            );
        }
        
        return (
            <div className="w3-container w3-card" style={{paddingTop:'15px'}}>
                <div className="w3-bar w3-round-small w3-teal">
                    {buttons}                    
                </div>
                <VisibleTab tabs={this.state.tabs}/>
            </div>
            
        );
    }
}

export default ButtonTabs;


