import React, { Component } from 'react';
import { NavLink, Switch, Route } from 'react-router-dom'
// sample props 
// {
//     parent: 'workexperience',
//     tabs:[
//       {
//         name:'Security Automation Developer', 
//         contents:['a', 'b', 'c' ], 
//         period:'Mar 2017 - Current'
//       },
//       {
//         name:'Sr. Software Developer', 
//         contents:[ 'x', 'y' ], 
//         period:'Jul 2016 - Feb 2017'
//       }
//   ]}
// }

class RoutedLinksTabs extends Component {

    constructor(props) {
        super (props)
        //setting tabs state
        const tabs = [];
        for(var l=0; l<props.tabs.length; l++){
            const tempTab = Object.assign({}, props.tabs[l]);
            if(l){
                tempTab.active = false
            }else{
                tempTab.active = true
            }
            tempTab.key = l
            tabs.push(tempTab)
        }
        this.state = {tabs}
    }

    render() {
        //creating all not first Links
        let remTabs = Object.assign([], this.state.tabs)//this remTabs gets final value in next line
        remTabs.shift()
        const restLinks = remTabs.map((tab) =>
            <NavLink activeClassName="w3-button-active" to={`/${this.props.parent}/${tab.key}`} style={{margin:'3px', textDecoration: 'none'}} key = {tab.key} className = 'w3-hover-green w3-bar-item w3-round w3-small'>{tab.name}</NavLink>
        );
        
        //A HELPER React COMPONENT
        function ToParagraphs (props){
            const paragraphs = props.contents.map((content, i) =>
                <p key={i} style={{textAlign:'justify'}}>{content}</p>
            );
            return (
                <div>{paragraphs}</div>
            );
        }
        
        //required in routed components
        const tabs = this.state.tabs;

        //RouterComponent recieving different kind of props
        function routedTab(props){
            //getting matched tabNum
            let tabNum;
            if(props.match.params.number){
                tabNum = parseInt(props.match.params.number, 0)
            }else{
                tabNum = 0
            }
            //getting matched tab
            let selTab = {};
            for(var l=0; l<tabs.length; l++){
                const tempTab = tabs[l]
                if(tempTab.key === tabNum){
                    selTab = Object.assign({}, tempTab)
                }
            }
            return (
                <div>
                    <h4 className="w3-text-grey w3-padding-16"><a href={selTab.href} target="_blank" rel="noopener noreferrer">{selTab.name}</a></h4>
                    {selTab.period ? <h6 className="w3-text-teal"><i className="fa fa-calendar fa-fw w3-margin-right"></i>{selTab.period}</h6>: ''}
                    <ToParagraphs contents={selTab.contents} />
                </div>
            );
        }

        //first separate NavLink to work root path
        //rest NavLinks from restLinks
        return (
            <div className="w3-container w3-card" style={{paddingTop:'15px'}}>
                <div className="w3-bar w3-round-small w3-teal">
                    <NavLink activeClassName="w3-button-active" exact to='/' style={{margin:'3px', textDecoration: 'none'}}  className = 'w3-hover-green w3-bar-item w3-round w3-small'>{this.props.tabs[0].name}</NavLink>
                    {restLinks}                    
                </div>
                <Switch>                    
                    <Route path={`/${this.props.parent}/:number`} component={routedTab} />
                    <Route path={`/`} component={routedTab} />
                </Switch>
            </div>
            
        );
    }
}

export default RoutedLinksTabs;


