import React, { Component } from 'react';
import ProfilePic from './LeftColumn/ProfilePic'
import ProfileHighlights from './LeftColumn/ProfileHighlights'

class LeftColumn extends Component {
  render() {
    return (
        <div className="w3-third">
            <div className="w3-white w3-text-grey w3-card-4">
                <ProfilePic name='Nameesh Kumar'/>
                <ProfileHighlights />
            </div>
        </div>
    );
  }
}

export default LeftColumn;


