import React, { Component } from 'react';
import RoutedLinksTabs from './WorkExperience/RoutedLinksTabs'

class WorkExperience extends Component {
  constructor(props){
    super(props)
    this.state = {
      parent: 'workexperience',
      tabs:[
        {
          name:'Security Automation Developer', 
          contents:[
            
            'Currently, I am working as Security Automation Developer for Pytheus Tek Consultancy, \n'+
            'Bangalore at ABInBev, GCC, Bangalore. I am part of a 40 people team running Next-Gen Security Operations \n'+
            'Center with goal to provide Great security to ABInBev. We have a big Infrastructure with tools CylanceProtect, \n'+
            'CrowdStrike, CyberArk, Proofpoint email-security, Palo Alto, zscaler, Sumo Logic for Machine Data Analytics, \n'+
            'Jira for Issues and Project tracking and Phantom as SECURITY AUTOMATION & ORCHESTRATION platform.',

            'Phantom comes as Cent OS ova file. I started this project learning Amazon Web Services by deploying Phantom to aws. \n'+
            'Phantom brings playbooks, which can be written to perform different combinations of actions on configured Assets. \n'+
            'We have playbooks which get necessary security events from Sumo Logic, manage those events and perform more actions \n'+
            'like getting virus total score, IP reputation, domain reputation, geo locate IP based on the event and the playbooks, \n'+
            'then alerts full Informational Security Events in Jira.',

            'I wrote a script which works as scheduler for playbooks using Phantom provided Rest API. The script has further \n'+
            'evolved to a Node.js server from where the scheduler can be managed over UI. The server is created using express \n'+
            'framework and socket.io is used to broadcast success/failure messages of Phantom API calls. It also sends email-alert \n'+
            'in event of API failure or playbook failure. It helped debugging Phantom playbooks by providing the error messages. \n'+
            'Front-End is written using require.js as AMD loader and backbone views. The same web server also provides \n'+
            'Rest API for Jira project tracking data and the same is consumed to show it as Charts on browser. \n'+
            'Server also features scheduling reoccurring Jira issues.',

            'Further explored the Rest APIs provided by mentioned tools for automated reports.',

            'Developed a service to automate processing of new emails. It is used to automate attachment of an email to a Jira issue \n'+
            'if subject of the email contains a valid Jira issue id and for processing Palo Alto firewalls \n'+
            'system alerts to Jira issues.'
          
          ], 
          period:'Mar 2017 - Current'
        },
        {
          name:'Sr. Software Developer', 
          contents:[
            
            'Worked as Web Developer for Delasoft Pvt Ltd, Hyderabad to develop the HPMS (Highway Performance Management System) \n'+
            'for State of Delaware, U.S. This required creating a web application based Management System for Data Collection, \n'+
            'Validation and Reports Generation. This Project introduced me Java web development technologies like J2EE, \n'+
            'Java Frameworks including Hibernate & Spring, HTML, JavaScript etc.',
          
            'Java was used for Back-End to provide the Rest API. Spring, Hibernate as main Frameworks were used \n'+
            'along with helping libraries for logging, mapping request Data etc. This helped me understanding \n'+
            'Model View Control architectural pattern, Annotations for Dependency Injection provided by the Spring \n'+
            'and sessionFactory provided by Hibernate.',

            'As the Application was created as Single Page Application, I learned Front-End technologies like Dojo Toolkit as AMD Loader, \n'+
            'Backbone for views, Kendo UI for grids, drag and drops web components, Joint for Directed graph, \n'+
            'Jasmine for JavaScript testing etc. I also came to know jQuery, Underscore.js and Animate.css like helping libraries.'

          ], 
          period:'Jul 2016 - Feb 2017'
        },
        {
          name:'Physics Faculty', 
          contents:[

            'Worked as Physics Foundation Faculty for IITian’s Pace Education Limited, Mumbai. \n'+
            'This helped me go through the laws of Nature once again.',
            
            'Worked as Physics Faculty for Achiever\'s Academy, Jalandhar.'
            
          ], 
          period:'Jul 2015 - May 2016'
        },
        {
          name:'Asst. Manager Drilling', 
          contents:[
            
            'My professional journey started as Management Trainee (Mechanical) at Coal India Limited. \n'+
            'I was moved to Drilling discipline posted as Drilling Engineer in Bisrampur Coal Exploration Camp, Ambikapur. \n'+
            'My responsibilities were to look after the repair, maintenance and reporting of Plant and Machinery at Camp. \n'+
            'The Plant and Machinery included Coring Drill Rigs, Positive Displacement Pumps, Trucks, Jeeps etc.',

            'After a year of my joining, I was given the responsibility of Officer-In-Charge to lead the same Exploration Camp \n'+
            'of nearly 60 people where I continued till Mar 2012. At the same time, I had some Excels with VBA macros ready, \n'+
            'which were automatically pushing the daily data of my exploration camp to Ranchi HQ, website. \n'+
            'I also wrote an Excel workbook to calculate Strike and Dip of a Coal Seam. \n'+
            'To work more on reports, data management I was posted at Regional Institute, Bilaspur. ',

            'At Bilaspur, I created many Excel workbooks with VBA macros for reporting and data keeping. \n'+
            'The best one was for Geological Report creation. It helped the creation of Geological Reports \n'+
            'easily with GUI based controls provided by User Forms using VBA. \n'+
            'I also created VBA macros for automatic AutoCad drawings of graphical lithologs and \n'+
            'borehole location plots using data from Excel Geological Reports.'

          ], 
          period:'Sep 2008 - May 2015'
        }
    ]}
  }
  render() {
    return (
        <div className="w3-container w3-card-2 w3-white w3-margin-bottom" style={{paddingBottom:'15px'}}>
            <h2 className="w3-text-grey w3-padding-16"><i className="fa fa-suitcase fa-fw w3-margin-right w3-xxlarge w3-text-teal"></i>Work Experience</h2>
            <RoutedLinksTabs tabs = {this.state.tabs} parent = {this.state.parent}/>
        </div>
    );
  }
}

export default WorkExperience;


