import React, { Component } from 'react';
import ButtonTabs from './Education/ButtonTabs'

class Education extends Component {
  
  constructor(props){
    super(props)
    this.state = {
      parent: 'education',
      tabs:[
        {
          name:'IIT Kanpur',
          contents:['Bachelor of Technology in Mechanical Engineering'], 
          period:'Jul 2003 - May 2008', 
          href:'http://www.iitk.ac.in/'
        },
        {
          name:'DAV College Jalandhar', 
          contents:['Senior Secondary'], 
          period: 'Apr 2001 - Mar 2003', 
          href:'http://www.davjalandhar.com/'
        }
      ]
    }
  }

  render() {
    return (
        <div className="w3-container w3-card-2 w3-white w3-margin-bottom" style={{paddingBottom:'15px'}}>
            <h2 className="w3-text-grey w3-padding-16">
              <i className="fa fa-book fa-fw w3-margin-right w3-xxlarge w3-text-teal"></i>Education
            </h2>
            <ButtonTabs tabs = {this.state.tabs} parent = {this.state.parent}/>
        </div>
    );
  }
}

export default Education;


